import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { UserInfos, UserService } from './../../../core/services/user.service';
import { LangService } from '@app/modules/core/services/references/lang.service';
import { UserConfigService } from '@app/modules/core/services/references/user-configuration.service';

@Component({
  selector: 'itx-childrens-informations',
  templateUrl: './childrens-informations.page.html',
  styleUrls: ['./childrens-informations.page.scss']
})
export class ChildrenInformationPage implements OnInit {

  public userInfos: UserInfos;
  public id: number;

  public currentChildren;
  public currentChildrenID;
  public translation: any;
  public userConfig: any;

  constructor(
    private userService: UserService,
    private route: ActivatedRoute,
    private langService: LangService,
    private userConfigService: UserConfigService
  ) {
    this.translation = this.langService.lang;
    this.userConfig = this.userConfigService.userconfig;
  }

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.id = params['id'];
      this.getUserChildrenById();
    });
  }

  private getUserChildrenById(): void {
    this.userService.getUserInfos().subscribe(userInfos => {
      this.userInfos = userInfos;

      for (let i = this.userInfos.Children.length - 1; i >= 0; i--) {
        if (this.userInfos.Children[i].ID == this.id) {
          this.currentChildren = this.userInfos.Children[i];
          this.currentChildrenID =  this.currentChildren.ID;
          return false;
          
        }
      }
    });
  }
}
