import { Component, OnInit, OnDestroy } from '@angular/core';

import { UserInfos, UserService } from './../../../core/services/user.service';
import { GendersService } from '@app/modules/core/services/references/genders.service';
import { RelationshipsService } from '@app/modules/core/services/references/relationships.service';
import { Subscription } from 'rxjs';
import { LangService } from '@services/references/lang.service';
import { UserConfigService } from '@app/modules/core/services/references/user-configuration.service';

@Component({
  selector: 'itx-emergency-contact',
  templateUrl: './emergency-contact.page.html',
  styleUrls: ['./emergency-contact.page.scss']
})
export class EmergencyContactPage implements OnInit, OnDestroy {

  public userInfos: UserInfos;
  public genderList: any[];
  public relatioonshipsList: any[];
  public translation;

  private userInfosObs: Subscription;
  private genderSub: Subscription;
  private relationshipsSub: Subscription;
  public userConfig: any;

  constructor(
    private userService: UserService,
    private genderService: GendersService,
    private relationshipsService: RelationshipsService,
    private langService: LangService,
    private userConfigService: UserConfigService
  ) {
    this.translation = this.langService.lang;
    this.userConfig = this.userConfigService.userconfig;
    this.userInfosObs = this.userService.userInfos$.subscribe(userInfos => {
      this.userInfos = userInfos;
    });

    this.genderSub = this.genderService.genders$.subscribe(res => {
      this.genderList = res;
    });

    this.relationshipsSub = this.relationshipsService.getRelationships().subscribe(res => {
      this.relatioonshipsList = res;
    });
  }

  ngOnInit(): void {
    this.userService.getUserInfos().subscribe();
    this.genderService.getGenders();
  }

  ngOnDestroy(): void {
    this.userInfosObs.unsubscribe();
    this.relationshipsSub.unsubscribe();
    this.genderSub.unsubscribe();
  }

}
