import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';

import { UserInfos, UserService } from './../../../core/services/user.service';
import { LangService } from '@services/references/lang.service';
import { UserConfigService } from '@app/modules/core/services/references/user-configuration.service';

@Component({
  selector: 'itx-partner-informations',
  templateUrl: './partner-informations.page.html',
  styleUrls: ['./partner-informations.page.scss']
})
export class PartnerInformationPage implements OnInit, OnDestroy {

  public userInfos: UserInfos;
  public translation: any;
  private userInfosObs: Subscription;
  public userConfig: any;

  constructor(
    private userService: UserService,
    private langService: LangService,
    private userConfigService: UserConfigService
  ) {
    this.translation = this.langService.lang;
    this.userConfig = this.userConfigService.userconfig;
    this.userInfosObs = this.userService.userInfos$.subscribe(userInfos => {
      this.userInfos = userInfos;
    });
  }

  ngOnInit(): void {
    this.userService.getUserInfos().subscribe();
  }

  ngOnDestroy(): void {
    this.userInfosObs.unsubscribe();
  }

}
