import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ProfileHomePage } from './pages/profile-home/profile-home.page';
import { PersonalInformationsPage } from './pages/personal-informations/personal-informations.page';
import { ProfessionalInformationsPage } from './pages/professional-informations/professional-informations.page';
import { FinancialInformationsPage } from './pages/financial-informations/financial-informations.page';
import { EmergencyContactPage } from './pages/emergency-contact/emergency-contact.page';
import { PartnerInformationPage } from './pages/partner-informations/partner-informations.page';
import { ChildrenInformationPage } from './pages/childrens-informations/childrens-informations.page';

const routes: Routes = [
  {
    path: '',
    component: ProfileHomePage
  },
  {
    path: 'personal-informations',
    component: PersonalInformationsPage,
    data: {
      title: 'profile.personalInfo.title',
      goBack: true
    }
  },
  {
    path: 'professional-informations',
    component: ProfessionalInformationsPage,
    data: {
      title: 'profile.professionalInfo.title',
      goBack: true
    }
  },
  {
    path: 'financial-informations',
    component: FinancialInformationsPage,
    data: {
      title: 'profile.financiaryTitle.title',
      goBack: true
    }
  },
  {
    path: 'emergency-contact',
    component: EmergencyContactPage,
    data: {
      title: 'profile.emergencyInfo.title',
      goBack: true
    }
  },
  {
    path: 'partner-informations',
    component: PartnerInformationPage,
    data: {
      title: 'profile.partnerInfo.title',
      goBack: true
    }
  },
  {
    path: 'childrens-informations/:id',
    component: ChildrenInformationPage,
    data: {
      title: 'profile.childrensInfo.title',
      goBack: true
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MyProfileRoutingModule { }
