import { Component, OnInit, OnDestroy } from '@angular/core';
import { trigger, style, animate, transition, query, stagger, keyframes, animateChild } from '@angular/animations';
import { DomSanitizer } from '@angular/platform-browser';

import { UserInfos, UserService } from './../../../core/services/user.service';
import { ProfileService } from '@app/modules/core/services/profile.service';
import { Subscription } from 'rxjs';
import { LangService } from '@services/references/lang.service';
import { UserConfigService } from '@app/modules/core/services/references/user-configuration.service';

@Component({
  selector: 'itx-profile-home',
  templateUrl: './profile-home.page.html',
  styleUrls: ['./profile-home.page.scss'],
  animations: [
    trigger('slide', [
      transition(
        ':enter',
        [animate('250ms ease-in-out', keyframes([
          style({ opacity: 0, offset: 0 }),
          style({ opacity: 1, offset: 1 })
        ]))]
      ),
      transition(
        ':leave',
        [animate('250ms ease-in-out', keyframes([
          style({ opacity: 1, offset: 0 }),
          style({ opacity: 0, offset: 1 })
        ]))]
      )
    ]),
    trigger('showSection', [
      transition(':leave', [query(':leave', stagger(50, [animateChild()]), { optional: true })]),
      transition(':enter', [query(':enter', stagger(50, [animateChild()]), { optional: true })])
    ]),
  ]
})
export class ProfileHomePage implements OnInit, OnDestroy {

  public userInfos: UserInfos;
  public adminPic: any;
  public userChildrens;
  public translation;
  private userInfoObs;

  private adminPictureSub: Subscription;
  userConfig: any;

  constructor(
    private userInfosService: UserService,
    private profileService: ProfileService,
    private sanitizer: DomSanitizer,
    private langService: LangService,
    private userConfigService: UserConfigService
  ) {
    this.translation = this.langService.lang;
    this.userChildrens = [];
    this.userConfig = this.userConfigService.userconfig;
    this.userInfoObs = this.userInfosService.userInfos$.subscribe(userInfos => {
      this.userInfos = userInfos;
      this.userChildrens = this.userInfos.Children;
    });

    this.adminPictureSub = this.profileService.getAdminPicture().subscribe(res => {
      this.adminPic = this.sanitizer.bypassSecurityTrustStyle('url(data:image/png;base64,' + res._body + ')');
    });
  }

  ngOnInit(): void {
    this.userInfosService.getUserInfos().subscribe();
  }

  ngOnDestroy(): void {
    this.userInfoObs.unsubscribe();
    this.adminPictureSub.unsubscribe();
  }

}
